@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/cormorantgaramond.woff2") format("woff2"), url("../fonts/cormorantgaramond.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/cormorantgaramondbold.woff2") format("woff2"), url("../fonts/cormorantgaramondbold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/cormorantgaramonditalic.woff2") format("woff2"), url("../fonts/cormorantgaramonditalic.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/poppinsregular.woff2") format("woff2"), url("../fonts/poppinsregular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/poppinsbold.woff2") format("woff2"), url("../fonts/poppinsbold.woff") format("woff");
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

body {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  background-color: #ffffff;
  color: #1D1613;
  margin: auto;
  min-width: 320px; }

h2 {
  font-family: "Cormorant Garamond", "Times New Roman", serif; }

a {
  text-decoration: none; }

li {
  list-style: none; }

.footer {
  background-color: #000000;
  display: flex;
  justify-content: center; }

.footer__wrapper {
  max-width: 1366px;
  display: flex;
  padding: 100px 98px;
  align-items: center;
  width: 100%;
  justify-content: space-between; }

.footer__main {
  color: #ffffff;
  width: 42%; }
  .footer__main h2 {
    font-size: 34px;
    line-height: 44.2px;
    letter-spacing: 0.03em;
    padding-bottom: 25px;
    margin: 0; }
  .footer__main p {
    color: #9B9997;
    line-height: 20.8px;
    padding-bottom: 52px;
    margin: 0; }

.footer__email-form {
  border-bottom: 1px solid #9B9997;
  display: flex;
  justify-content: space-between; }
  .footer__email-form input {
    background-color: transparent;
    border-color: transparent;
    width: 62%;
    padding: 15px 0 15px;
    color: #ffffff; }
    .footer__email-form input::placeholder {
      font-size: 14px;
      line-height: 21px; }
  .footer__email-form button {
    color: #ffffff;
    background-color: transparent;
    border: none;
    padding: 15px 0 15px;
    cursor: pointer;
    display: flex;
    flex-wrap: nowrap;
    align-items: center; }
    .footer__email-form button:hover {
      color: #a97c62; }
  .footer__email-form svg {
    padding-left: 12px; }

.footer__nav {
  display: flex;
  justify-content: space-between;
  width: 32%; }
  .footer__nav ul {
    margin: 0;
    padding: 0;
    padding-right: 20px; }
  .footer__nav a {
    color: #ffffff;
    font-weight: 500;
    line-height: 20.8px; }
    .footer__nav a:hover {
      color: #a97c62; }
  .footer__nav li {
    padding-bottom: 30px; }
    .footer__nav li:last-child {
      padding-bottom: 0; }

.footer__social {
  width: 115px; }
  .footer__social a {
    color: #ffffff;
    display: flex;
    flex-wrap: nowrap; }
    .footer__social a:hover {
      color: #a97c62; }
  .footer__social p {
    margin: 0; }
  .footer__social svg {
    padding-right: 18px; }
  .footer__social ul {
    margin: 0;
    padding: 0; }
  .footer__social li {
    padding-bottom: 30px; }
    .footer__social li:last-child {
      padding-bottom: 0; }

@media (max-width: 1023px) {
  .footer__wrapper {
    flex-wrap: wrap;
    padding: 60px 150px 60px 45px; }
  .footer__main {
    width: 100%;
    padding-bottom: 50px; }
  .footer__nav {
    width: 65%; }
  .footer__email-form input {
    width: 75%;
    max-width: 100%; } }

@media (max-width: 767px) {
  .footer__wrapper {
    padding: 60px 15px;
    flex-direction: column;
    align-items: start; }
  .footer__main h2 {
    padding-bottom: 30px; }
  .footer__main p {
    line-height: 22.4px;
    padding-bottom: 50px; }
  .footer__email-form input {
    width: 61%; }
  .footer__nav {
    flex-direction: column; }
    .footer__nav ul {
      padding-bottom: 50px; }
  .footer__social ul {
    display: flex; }
  .footer__social li {
    padding-bottom: 0; }
  .footer__social p {
    display: none; }
  .footer__social svg {
    padding-right: 30px;
    height: 21px; } }

.pictures {
  display: flex;
  justify-content: center; }

.pictures__wrapper {
  max-width: 1366px;
  padding: 108px 98px 130px; }

.pictures__title {
  text-align: center;
  padding-bottom: 60px; }
  .pictures__title h2 {
    font-size: 42px;
    line-height: 44.6px;
    letter-spacing: 0.03em;
    margin: 0;
    padding-bottom: 35px; }
  .pictures__title span {
    font-size: 42px;
    line-height: 54.6px; }
  .pictures__title a {
    font-weight: 500;
    line-height: 20.8px;
    color: #1D1613;
    border-bottom: 1px solid #a97c62;
    padding-bottom: 10px; }
    .pictures__title a:hover {
      color: #a97c62; }

.pictures__photo {
  width: 1170px;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  grid-auto-rows: 194px;
  grid-auto-flow: dense; }
  .pictures__photo li:nth-child(5),
  .pictures__photo li:nth-child(6),
  .pictures__photo li:nth-child(7) {
    margin-top: auto; }
  .pictures__photo li:nth-child(6) {
    grid-column-start: 3;
    grid-row-start: 2; }
  .pictures__photo li:nth-child(7) {
    grid-column-end: -1;
    margin-left: -13px; }

@media (max-width: 1023px) {
  .pictures__title h2 {
    font-size: 34px;
    line-height: 44.2px; }
  .pictures__wrapper {
    padding: 84px 45px 80px; } }
